export const fetchJsonString = async(path: string) => {
    const d = await fetch(`${window.location.href + path}`, {mode: 'cors'});  
    const json = await d.json();
    return json
}

export const fetchJsonStringLocal = async(path: string) => {
    const d = await fetch(`${window.location.href + path}`, {mode: 'cors'});  
    const json = await d.json();
    return json
}
