export const fetchCSVString = async(path: string) => {
    const d = await fetch(`${window.location.href + path}`, {mode: 'cors'});  
    const txt = await d.text();
    return txt
}
export const getCSV = async(path: string = 'https://raw.githubusercontent.com/NamjuLee/data/master/color/color-data.csv') => {
    const d = await fetch(`${path}`, {mode: 'cors'});  
    const colString = await d.text()

    let texts = colString.split(/\r\n|\n/);
    let lines = [];
    for (let i = 0; i < texts.length; ++i) {
        let theData = texts[i].split(',');
        let row = [];
        for (let j = 0; j < theData.length; ++j) {
            if (Number.isNaN(theData[j])){
                row.push(+theData[j]);
            } else {
                row.push(theData[j]);
            }
        }
        lines.push(row);
    }
  
//     console.log('color mat :', lines)
}