export class Describe {
    index: string;
    data: number[];
    count: number;
    min: number;
    max: number;
    mean: number;
    sum: number;
    median: number;
    variance: number;
    STD: number;
    toFixed: number;
    constructor(data: number[], count: number, min: number, max: number, mean: number, sum: number, median: number, variance: number, STD: number, index: string) {
        this.data = data;
        this.count = count;
        this.min = min;
        this.max = max;
        this.mean = mean;
        this.sum = sum;
        this.median = median;
        this.variance = variance;
        this.STD = STD;
        this.index = index;
        this.toFixed = 4;
    }
    ToString() {
        const s: string[] = [];
        s.push('index : ' + this.index);
        s.push('count : ' + this.count);
        s.push('min : ' + this.min.toFixed(this.toFixed));
        s.push('max : ' + this.max.toFixed(this.toFixed));
        s.push('mean : ' + this.mean.toFixed(this.toFixed));
        s.push('sum : ' + this.sum.toFixed(this.toFixed));
        s.push('median : ' + this.median.toFixed(this.toFixed));
        s.push('variance : ' + this.variance.toFixed(this.toFixed));
        s.push('STD : ' + this.STD.toFixed(this.toFixed));
        return s;
    }
}
export class StatisticsUtility {

    static DescribeNumeric(n: number[], index: string = 'null') {
        let describe = new Describe(
            n,
            n.length,
            StatisticsUtility.Min(n),
            StatisticsUtility.Max(n),
            StatisticsUtility.Mean(n),
            StatisticsUtility.Sum(n),
            StatisticsUtility.Median(n),
            StatisticsUtility.Variance(n),
            StatisticsUtility.StandardDeviation(n),
            index
        );
        return describe;
    }
    static Min(n: number[]) { return Math.min(...n); }
    static Max(n: number[]) { return Math.max(...n); }
    static Mean(n: number[]) { return StatisticsUtility.Average(n); }
    static Average(n: number[]) { let sum = StatisticsUtility.Sum(n); return sum / n.length; }
    static Sum(n: number[]) {
        let i = n.length;
        let sum = 0.0;
        while (i--) { sum += n[i]; }
        return sum;
    }
    static Median(n: number[]) {
        let nn = StatisticsUtility.SortAscending(n);
        let half = Math.floor(nn.length / 2);
        if (nn.length % 2) { return nn[half]; } else { return (nn[half - 1] + nn[half]) / 2.0; }
    }
    static SortAscending(n: number[]) {
        return n.sort((a: number, b: number) => { return a - b; });
    }
    static SortDescending(n: number[]) {
        return n.sort((b: number, a: number) => { return a - b; });
    }
    static Variance(n: number[]) {
        let mean = StatisticsUtility.Mean(n);
        let sum = 0;
        for (let i = 0; i < n.length; ++i) {
            if (n[i] < mean) { sum += Math.pow(mean - n[i], 2); } else { sum += Math.pow(mean - n[i], 2); }
        }
        return sum / (n.length - 1.0);
    }
    static StandardDeviation(n: number[]) {
        let variance = StatisticsUtility.Variance(n);
        return Math.sqrt(variance);
    }

    // ............................................................................
    static GetDataForHistDataFromNumeric(n: number[], bin: number = 5) {
        let bins: number[][] = [];
        let d = StatisticsUtility.SortAscending(n);
        let domain = [d[0], d[d.length - 1]];
        let interval = (domain[1] - domain[0]) / (bin - 1);
        let start = 0;
        for (let i = 0; i < bin; ++i) {
            const maxNum = domain[0] + ((i + 1) * interval);
            let data: number[] = [];
            for (let j = start; j < n.length; ++j) {
                if (n[j] < maxNum) { data.push(n[j]); } else { start = j; break; }
            }
            bins.push(data);
        }
        return bins;
    }
    public static GetIndexByDataForHistDataFromNumeric(n: number[][], v: number) {
        for (let i = 0; i < n.length; ++i) {
            for (let j = 0; j < n[i].length; ++j) {
                if (v < n[i][j]) {
                    return i;
                }
            }
        }
        return -1;
    }
}


export const getMedian = (arr: number[]) => {
    if (!arr?.length) { return 0 }
    const sorted = arr.sort((a, b) => a - b)
    const mid = Math.floor(sorted.length / 2)
    return sorted.length % 2 !== 0 ? sorted[mid] : (sorted[mid - 1] + sorted[mid]) / 2
}

