//// reference
// https://stackoverflow.com/questions/12024770/access-camera-from-a-browser
// https://www.html5rocks.com/en/tutorials/getusermedia/intro/
// https://www.youtube.com/watch?v=nCrQ1A2BEZ0
export const SIZE = { // https://en.wikipedia.org/wiki/Display_resolution
    FULL_HD: [1920, 1080],
    HD: [1280, 720],
    NTSC: [720, 480],

    WXGA: [1080, 720],
    XGA: [1024, 768],
    SVG: [800, 480]
};
export class Camera {
    public static id: number = 0;
    public static FILTERS = [
        'grayscale',
        'sepia',
        'blur',
        'brightness',
        'contrast',
        'hue-rotate',
        'hue-rotate2',
        'hue-rotate3',
        'saturate',
        'invert',
        ''
    ];

    video: HTMLVideoElement;
    canvas: HTMLCanvasElement;
    ctx: CanvasRenderingContext2D;

    width: number = 400; // 1280; // 400;
    height: number = 400; // 720; // 400;

    mediaStream: MediaStream;

    hostDiv: HTMLElement;
    id: string;

    public static GetCamera() {
        // let canvas: HTMLCanvasElement = document.createElement('canvas') as HTMLCanvasElement;
        // let ctx = canvas.getContext('2d');
        // let videoUrl = window.URL; // || window.webkitURL;

        // navigator.getUserMedia = navigator.get
    }
    constructor(div: HTMLElement) {
        this.hostDiv = div;
        this.width = div.clientWidth;
        this.height = div.clientHeight;
        this.id = 'njslab-web-core-camera' + Camera.id++;

        const d = SIZE.FULL_HD;
    //     console.log(d);
        // var constraints = {
        //     audio: false,
        //     video: {
        //         facingMode: facingMode
        //     }
        // };

        this.InitVideo(div);
        this.InitMedia();
    }
    public SetSizeFromTemplate(size: number[]) {
        this.width = size[0]; this.height = size[1];
        this.video.style.width = size[0] + 'px'; this.video.style.height = size[1] + 'px';
        this.hostDiv.style.width = size[0] + 'px'; this.hostDiv.style.height = size[1] + 'px';
    }
    public InitCanvas() {
        const canvas = document.createElement('canvas');
        if (canvas) {
            this.canvas = canvas;
            const ctx = this.canvas.getContext('2d');
            if (ctx) { this.ctx = ctx; }
        }
    }
    public InitVideo(div: HTMLElement) {
        this.video = document.createElement('video');
        this.video.id = this.id;
        this.video.setAttribute('playsinline', '');
        this.video.setAttribute('autoplay', '');
        this.video.setAttribute('muted', '');
        // this.video.className = Camera.FILTERS[2];
        this.video.style.filter = 'blur(3px)';
        this.video.style.filter = 'contrast(150%)';
        this.video.style.width = this.width + 'px';
        this.video.style.height = this.height + 'px';

        div.appendChild(this.video);
    }
    public InitMedia() {
        const constraints = {
            video: {
                facingMode: 'facingMode',
                // width: Camera.SIZE.FULL_HD[0],
                // height: Camera.SIZE.FULL_HD[1],
                width: this.width,
                height: this.height,
            },
            audio: false
        };

        if (this.HasGetUserMedia()) {
            navigator.mediaDevices.getUserMedia(constraints).then((stream: MediaStream) => {
                this.mediaStream = stream;
                this.video.srcObject = this.mediaStream;
            });
        } else {
            alert('getUserMedia() is not supported by your browser');
        }
    }
    public GetImageData() {
        this.ctx.drawImage(this.video, 0, 0, this.width, this.height);
        let frame = this.ctx.getImageData(0, 0, this.width, this.height);
    //     console.log(frame);
    }
    public Resize(width: number, height: number) {
        this.width = width; this.height = height;
        this.video.style.width = width + 'px';
        this.video.style.height = height + 'px';
    }
    public HasGetUserMedia() {
        return !!(navigator.mediaDevices && navigator.mediaDevices.getUserMedia);
    }
    public Dummy() {
        //
    }
    public Dispose() {
        // this.mediaStream.stop();
        this.mediaStream.getTracks().forEach(track => track.stop());

        // Array.from(document.getElementsByClassName(this.id)).forEach(
        //     (element) => { //   (element, index, array) => {
        //         this.hostDiv.removeChild(element);
        //     }
        // );

        this.hostDiv.removeChild(this.video);
    }
}