/////////////////// PixelMapGlobe class
export class PixelMapGlobe {

    public listPixel: Pixel[] = [];
    public rx: number = 0;
    public ry: number = 0;
    public radius: number = 0;
    public ddx: number = 0.0;
    public ddy: number = 0.0;
    public v0: NVector3 = NVector3.Origin();
    public v1: NVector3 = NVector3.Origin();

    constructor(rx: number = 36, ry: number = 18, radius: number = 100) {
        this.rx = rx;
        this.ry = ry;
        this.radius = radius;
        this.ddx = 0.0;
        this.ddy = 0.0;
        // this.v0 = v0;
        // this.v1 = v1;
        // this.Built();
        this.Init();
    }
    Init() {
        this.ddx = Math.PI * 2 / this.rx;
        this.ddy = Math.PI / this.ry;
    //     console.log('ddx is ' + this.ddx + '. ddy is ' + this.ddx);
    //     console.log('Pixe Map was executed...');
        this.InitGrid();
    }
    InitGrid() {
        let vv = 0;
        for (let y = 0; y < Math.PI; y += this.ddy) {
            let uu = 0;
            for (let x = 0; x < Math.PI * 2; x += this.ddx) {

                let px = new Pixel(this);
                px.i = uu; px.j = vv;
                px.u = uu; px.v = vv;
                let pos = this.RemapToShpere(x, y);
                px.vec = new NVector3(pos[0], pos[1], pos[2]);
                px.norV = normalize(px.vec);

                pos = this.RemapToShpere(x - this.ddx * 0.5, y - this.ddy * 0.5);
                px.recVec.push(new NVector3(pos[0], pos[1], pos[2]));

                pos = this.RemapToShpere(x + this.ddx * 0.5, y - this.ddy * 0.5);
                px.recVec.push(new NVector3(pos[0], pos[1], pos[2]));

                pos = this.RemapToShpere(x + this.ddx * 0.5, y + this.ddy * 0.5);
                px.recVec.push(new NVector3(pos[0], pos[1], pos[2]));

                pos = this.RemapToShpere(x - this.ddx * 0.5, y + this.ddy * 0.5);
                px.recVec.push(new NVector3(pos[0], pos[1], pos[2]));

                // px.recVec.push(new NVector3(xx - this.ddx * 0.5, yy - this.ddy * 0.5, zz));
                // px.recVec.push(new NVector3(xx + this.ddx * 0.5, yy - this.ddy * 0.5, zz));
                // px.recVec.push(new NVector3(xx + this.ddx * 0.5, yy + this.ddy * 0.5, zz));
                // px.recVec.push(new NVector3(xx - this.ddx * 0.5, yy + this.ddy * 0.5, zz));
                // console.log(zz);

                this.listPixel.push(px);
                uu++;
            }
            vv++;
        }
        this.InitGridNeighbor();
    }
    RemapToShpere(x: number, y: number) {
        return [this.radius * Math.cos(x) * Math.sin(y),
        this.radius * Math.sin(x) * Math.sin(y),
        this.radius * Math.cos(y)];
    }
    InitGridNeighbor() {
        for (let j = 0; j < this.ry; ++j) {
            for (let i = 0; i < this.rx; ++i) {
                let k = j * this.rx + i;
                let px0 = this.listPixel[k];
                if (i > 0) { px0.nPix.push(this.listPixel[k - 1]); }
                if (i < this.rx - 1) { px0.nPix.push(this.listPixel[k + 1]); }
                if (j > 0) { px0.nPix.push(this.listPixel[k - this.rx]); }
                if (j < this.ry - 1) { px0.nPix.push(this.listPixel[k + this.rx]); }
                if ((i > 0) && (j < this.ry - 1)) { px0.nPix.push(this.listPixel[k + this.rx - 1]); }
                if ((i > 0) && (j > 0)) { px0.nPix.push(this.listPixel[k - this.rx - 1]); }
                if ((i < this.rx - 1) && (j < this.ry - 1)) { px0.nPix.push(this.listPixel[k + this.rx + 1]); }
                if ((i < this.rx - 1) && (j > 0)) { px0.nPix.push(this.listPixel[k - this.rx + 1]); }
            }
        }
    //     console.log('done for computing neighbors ');
    }
    GetPixelAtPoint(pt: NVector3) {
        let pixelMap = this;
        let dp = new NVector3(pt.x - pixelMap.v0.x, pt.y - pixelMap.v0.y, pt.z - pixelMap.v0.z);
        let i = Math.round(dp.x / pixelMap.ddx);
        let j = Math.round(dp.y / pixelMap.ddy);
        if (i >= pixelMap.rx) { i = pixelMap.rx - 1; }
        if (j >= pixelMap.ry) { j = pixelMap.ry - 1; }
        if (i < 0) { i = 0; }
        if (j < 0) { j = 0; }
        return pixelMap.listPixel[j * pixelMap.rx + i];
    }
    ////////////////// event
    MouseMove(x: number, y: number, z: number) {
        let pixelMap = this;
    //     console.log('mouseMove x:' + x + ', y:' + y + ', z:' + z);
        let cpt = pixelMap.GetPixelAtPoint(new NVector3(x, y, z));
    //     console.log(cpt.nPix);
        // VisBufferPointsFromVecs(cpt.ptsCenter, 100);
    }
    onDocumentMouseMove() {
    //     console.log('move');
    }
    onmouseenter() {
    //     console.log('enter');
    }
    ////////////////// visualization
    CVisLineGrid(ctx: CanvasRenderingContext2D) {
        let pixelMap = this;
        for (let i = 0; i < pixelMap.listPixel.length; ++i) {
            let color = '#ff0000';
            // console.log('d');
            ctx.lineWidth = 0.5;
            ctx.beginPath();
            ctx.moveTo(pixelMap.listPixel[i].recVec[0].x, pixelMap.listPixel[i].recVec[0].y);
            // console.log(pixelMap.listPixel[i].recVec)
            for (let j = 1; j < pixelMap.listPixel[i].recVec.length; ++j) {
                ctx.lineTo(pixelMap.listPixel[i].recVec[j].x, pixelMap.listPixel[i].recVec[j].y);
                // console.log(pixelMap.listPixel[i].nPix[j].vec.x, pixelMap.listPixel[i].nPix[j].vec.y);

            }
            ctx.closePath();
            ctx.strokeStyle = color; // ColorUtility.HexToCanvasColor(color, 1);
            ctx.stroke();
            // ctx.fill();
        }
    }
    VisMeshGrid() {
        let pixelMap = this;
        for (let i = 0; i < pixelMap.listPixel.length; ++i) {
            // VisMeshRectFromFourVecs(pixelMap.listPixel[i].ptsRectLst);
        }
    }
    VisCenterPoints() {
        let pixelMap = this;
        let pts: NVector3[] = [];
        for (let i = 0; i < pixelMap.listPixel.length; ++i) {
            pts.push(pixelMap.listPixel[i].vec);
        }
        // VisBufferPointsFromVecs(pts);
    }
    Render(ctx: CanvasRenderingContext2D) {
        /* */
    }
    GetTriFloatArray() { return new Float32Array(this.GetTriArray()); }
    GetTriArray() {
        let array: number[] = [];
        let p = NVector3.Origin();
        for (let i = 0; i < this.listPixel.length; ++i) {
            p = this.listPixel[i].recVec[0]; array.push(p.x, p.y, p.z);
            p = this.listPixel[i].recVec[3]; array.push(p.x, p.y, p.z);
            p = this.listPixel[i].recVec[2]; array.push(p.x, p.y, p.z);
            p = this.listPixel[i].recVec[0]; array.push(p.x, p.y, p.z);
            p = this.listPixel[i].recVec[2]; array.push(p.x, p.y, p.z);
            p = this.listPixel[i].recVec[1]; array.push(p.x, p.y, p.z);

        }
        return array;
    }
    GetPtFloatArray() { return new Float32Array(this.GetPtArray()); }
    GetPtArray() {
        let array: number[] = [];
        for (let i = 0; i < this.listPixel.length; ++i) {
            let p = this.listPixel[i].vec;
            array.push(p.x, p.y, p.z);
        }
        return array;
    }
    GetNormalFloatArray() { return new Float32Array(this.GetNormalArray()); }
    GetNormalArray() {
        let array: number[] = [];
        let p = NVector3.Origin();
        for (let i = 0; i < this.listPixel.length; ++i) {
            p = this.listPixel[i].recVec[0]; p.normalize(); array.push(p.x, p.y, p.z);
            p = this.listPixel[i].recVec[3]; p.normalize(); array.push(p.x, p.y, p.z);
            p = this.listPixel[i].recVec[2]; p.normalize(); array.push(p.x, p.y, p.z);
            p = this.listPixel[i].recVec[0]; p.normalize(); array.push(p.x, p.y, p.z);
            p = this.listPixel[i].recVec[2]; p.normalize(); array.push(p.x, p.y, p.z);
            p = this.listPixel[i].recVec[1]; p.normalize(); array.push(p.x, p.y, p.z);

        }
        return array;
    }
    GetTexCoordinFloatArray() { return new Float32Array(this.GetTexCoordinArray()); }
    GetTexCoordinArray() {
        let array: number[] = [];
        // let p = NVector3.origin;

        for (let i = 0; i < this.listPixel.length; ++i) {
            let u = this.listPixel[i].u / (this.rx - 1);
            let v = this.listPixel[i].v / (this.ry - 1);

            array.push(u, v);
            array.push(u, v + this.ddy);
            array.push(u + this.ddx, v + this.ddy);
            array.push(u, v);
            array.push(u + this.ddx, v + this.ddy);
            array.push(u + this.ddx, v);

            // px.recVec.push(new NVector3(xx - this.ddx * 0.5, yy - this.ddy * 0.5, zz));
            // px.recVec.push(new NVector3(xx + this.ddx * 0.5, yy - this.ddy * 0.5, zz));
            // px.recVec.push(new NVector3(xx + this.ddx * 0.5, yy + this.ddy * 0.5, zz));
            // px.recVec.push(new NVector3(xx - this.ddx * 0.5, yy + this.ddy * 0.5, zz));
        }
        // console.log('pppp');
        // console.log(array);
        return array;
    }
}

export class Pixel {
    static ID = 0;

    map: PixelMapGlobe;
    vec: NVector3;
    norV: NVector3;
    nPix: Pixel[] = [];
    recVec: NVector3[];
    i: number = -1;
    j: number = -1;
    k: number = -1;
    u: number = -1;
    v: number = -1;
    id: number;
    color: number[] = [];
    data: {};

    static GetID() { return Pixel.ID++; }

    constructor(map: PixelMapGlobe, v: NVector3 = new NVector3(0, 0, 0)) {
        this.map = map;
        this.vec = v;
        this.nPix = [];
        this.recVec = [];
        this.i = -1;
        this.j = -1;
        this.k = -1;
        this.id = Pixel.GetID();
        this.data = {};
    }
    ToString() {
        let temp = 'Pixel' + this.id;
    //     console.log(temp);
        return temp;
    }
    Update() {
        /* */
    }
    Render() {
        /* */
    }
}