import { NVector3 } from '../../../../..';
import { IndependentPanel } from '../IndependentPanel';
export class BodyDiv {
    static zIndex = 9999;
    host: IndependentPanel;
    vec: NVector3;

    divAnchor: HTMLDivElement;
    div: HTMLDivElement;
    divHost: HTMLDivElement;

    width: number;
    height: number;

    private xOff0: number = 5;
    private yOff0: number = 5;
    private xOff1: number = 25;
    private yOff1: number = 25;

    private _textAlign: string = 'left';
    private _textCol: string;
    private _content: string;

    static GetZIndex() {
        return (BodyDiv.zIndex--).toString();
    }

    constructor(host: IndependentPanel, x: number, y: number, width: number, height: number) {
        this.host = host;
        this.vec = new NVector3(x, y);
        this.width = width;
        this.height = height;
        // console.log(this.host.independentPanelWrapper.viewUIWrapper);
        this.divHost = this.host.independentPanelWrapper.nUI.divHost as HTMLDivElement;
        this.GetDivBaseContainer();
        this.textAlign = 'left';
        // this.textCol = this.host.renderer.currentStyle.textCol;
    }
    set textCol(v: string) {
        this._textCol = v;
        this.div.style.color = this._textCol;
    }
    set textAlign(v: string) {
        this._textAlign = v;
        this.div.style.textAlign = this._textAlign;
    }
    get Width() {
        return this.width;
    }
    set Width(v: number) {
        this.width = v;
        this.UpdateSize();
    }
    get Height() {
        return this.height;
    }
    set Height(v: number) {
        this.height = v;
        this.UpdateSize();
    }
    get content() {
        return this._content;
    }
    set content(v: string) {
        this._content = v;
        this.div.textContent = v;
    }
    Hide(v: boolean) {
        if (v) {
            this.Remove();
        } else {
            this.GetDivBaseContainer();
        }
    }

    Translate(xo: number, yo: number) {
        this.vec.x += xo; this.vec.y += yo;
        this.div.style.left = this.vec.x + this.xOff0 + 'px';
        this.div.style.top = this.vec.y + this.yOff0 + 'px';
    }
    UpdateSize() {
        this.div.style.width = this.width - this.xOff1 + 'px';
        this.div.style.height = this.height - this.host.HeadHeight - this.yOff1 + 'px';
    }
    Remove() {
        this.divHost.removeChild(this.div);
    }
    private GetDivBaseContainer() {
        let div = document.createElement('div');
        div.style.position = 'absolute';
        div.style.pointerEvents = 'none';
        div.id = `indePanel`;
        div.style.height = this.height - this.yOff1 + 'px';
        div.style.width = this.width - this.xOff1 + 'px';
        div.style.left = this.vec.x + this.xOff0 + 'px';
        div.style.top = this.vec.y + this.yOff0 + 'px';
        // div.style.background = ColorUtility.HexToCanvasColor('#ffffff', '0.5');
        div.style.zIndex = '10'; // BodyDiv.GetZIndex();
        div.style.padding = '6px';
        div.style.paddingLeft = '6px';
        div.style.wordWrap = 'break-word'; // word-wrap: break-word
        div.style.overflow = 'hidden'; // word-wrap: break-word
        div.textContent = this._content;
        div.style.font = '13px Arial';
        div.style.fontWeight = 'lighter';
        div.style.lineHeight = '23px';
        // div.style.background = '#ff0000';
        div.style.textAlign = this._textAlign;
        div.style.color = this.textCol;

        this.divHost.appendChild(div);
        this.div = div;
        return div;
    }
}