// https://developer.mozilla.org/en-US/docs/Web/API/Touch_events
import { ActiveCommandBase } from '../Core/ActiveCommand/ActiveCommandBase';
import { TOUCH_TYPE } from '../../AppCommon/Enum';
export class TouchEventData {
    x: number = -1; y: number = -1; z: number = -1;
    preX: number = -1; preY: number = -1; preZ: number = -1;
    yGLPicking: number;
    command: ActiveCommandBase;
    TOUCH_TYPE: TOUCH_TYPE = TOUCH_TYPE.MOVE;

    identifier: number;
    // touchType: Touch['touchType'];

    radiusX: number;
    radiusY: number;
    rotationAngle: number;
    force: number;

    color: string;
    // tslint:disable-next-line:no-any
    constructor() {
        // console.trace();
    }
    public DeepCopy() {
        const mEvent = new TouchEventData();
        mEvent.x = this.x; mEvent.y = this.y; mEvent.z = this.z;
        mEvent.preX = this.preX; mEvent.preY = this.preY; mEvent.preZ = this.preZ;

        mEvent.yGLPicking = this.yGLPicking;
        mEvent.TOUCH_TYPE = this.TOUCH_TYPE;
        mEvent.command = this.command;
        // mEvent.native = this.native;
        return mEvent;
    }
}
export class TouchEventDataList {
    // preEvent: TouchEventDataList;
    native: MouseEvent;
    eventList: TouchEventData[];
    TOUCH_TYPE: TOUCH_TYPE = TOUCH_TYPE.MOVE;
    // tslint:disable-next-line:no-any
    constructor() {
        this.eventList = [];
    }
    // Build(e: TouchEvent) {
    // tslint:disable-next-line:no-any
    public Build(e: any, tType: TOUCH_TYPE) {
        for (let i = 0; i < e.touches.length; ++i) {
            const tEvent = new TouchEventData();
            tEvent.identifier = e.touches[i].identifier;

            tEvent.TOUCH_TYPE = tType;

            tEvent.radiusX = e.touches[i].radiusX;
            tEvent.radiusY = e.touches[i].radiusY;

            tEvent.rotationAngle = e.touches[i].rotationAngle;
            tEvent.x = e.touches[i].clientX;
            tEvent.y = e.touches[i].clientY;
            tEvent.force = e.touches[i].force;

            tEvent.color = this.ColorForTouch(e.touches[i]);
            this.eventList.push(tEvent);
        }
        //    this.OngoingTouchIndexById()
    }
    public DeepCopy() {
        // return
    }
    // private OngoingTouchIndexById(idToFind: number, ongoingTouches: TouchList) {
    //     for (var i = 0; i < ongoingTouches.length; i++) {
    //         var id = ongoingTouches[i].identifier;
    //         if (id === idToFind) { return i; }
    //     }
    //     return -1;    // not found
    // }
    private ColorForTouch(touch: Touch) {
        const r = touch.identifier % 16;
        const g = Math.floor(touch.identifier / 3) % 16;
        const b = Math.floor(touch.identifier / 7) % 16;
        const rs = r.toString(16); // make it a hex digit
        const gs = g.toString(16); // make it a hex digit
        const bs = b.toString(16); // make it a hex digit
        const color = '#' + rs + gs + bs;
        // console.log('color for touch with identifier ' + touch.identifier + ' = ' + color);
        return color;
    }
}
export class TouchInteraction {
    public static touchEventDataPre: TouchEventDataList; // = new MouseEventData();

    isEnable: boolean = true;

    click: boolean = false;
    prevent: boolean = false;
    delay: number = 100;
    // timer: NodeJS.Timer | number;
    down: boolean = false;
    e: TouchEventData;
    preEvent: TouchInteraction | undefined;
    divHost: HTMLElement;
    public static CommonEventBuilder(e: TouchEvent, type: TOUCH_TYPE): TouchEventDataList {
        const mEvent: TouchEventDataList = new TouchEventDataList();
        mEvent.TOUCH_TYPE = type;
        mEvent.Build(e, type);

        this.touchEventDataPre = mEvent;
        return mEvent;
    }
    constructor(divHost: HTMLElement) {
        this.divHost = divHost;
        this.InitNativeTouchEvent(this.divHost);
    }
    public InitNativeTouchEvent(div: HTMLElement) {
        div.ontouchstart = (e: TouchEvent) => {
            const mEvent = TouchInteraction.CommonEventBuilder(e, TOUCH_TYPE.START);
            this.TouchEventEmittingBinding(mEvent);
        };
        div.ontouchend = (e: TouchEvent) => {
            const mEvent = TouchInteraction.CommonEventBuilder(e, TOUCH_TYPE.END);
            this.TouchEventEmittingBinding(mEvent);
        };
        div.ontouchmove = (e: TouchEvent) => {
            const mEvent = TouchInteraction.CommonEventBuilder(e, TOUCH_TYPE.MOVE);
            this.TouchEventEmittingBinding(mEvent);
        };
        div.ontouchcancel = (e: TouchEvent) => {
            const mEvent = TouchInteraction.CommonEventBuilder(e, TOUCH_TYPE.CANCEL);
            this.TouchEventEmittingBinding(mEvent);
        };
    }
    TouchEventEmittingBinding(mouseEventData: TouchEventDataList) {
    //     console.log('implmentation needed', mouseEventData);
    }
}