import { StatisticsUtility } from './Core/StatisticsUtility';

export class NGeoJSONFeatureService {

    static geoJsonGeoType = ['Point', 'MultiPoint', 'LineString', 'MultiLineString', 'Polygon', 'MultiPolygon', 'GeometryCollection'];
    // tslint:disable-next-line:no-any
    dJson: any;
    // tslint:disable-next-line:no-any
    dMat: any;
    // tslint:disable-next-line:no-any
    index: string[];
    dHist: number[][];
    dDescribe: string[];
    // tslint:disable-next-line:no-any
    array: {} = {};
    arrayVis: {} = {};

    static GetHistByCol(d: number[], numBin: number = 20): number[][] {
        // let data = this.GetDataByIndex(colIndex);
        return StatisticsUtility.GetDataForHistDataFromNumeric(d, numBin);
    }

    // tslint:disable-next-line:no-any
    static GetIndexFromGeoJson(d: any) {
        // tslint:disable-next-line:no-any
        let geometryGeoJson: any = [], propertiesGeoJson: any = [];
        Object.keys(d).forEach((key) => {
            if (key === 'features') {
                let theData = d[key];
                for (let data of theData) {
                    geometryGeoJson = data.geometry;
                    propertiesGeoJson = data.properties;
                    break;
                }
            }
        });
        let index: string[] = [];
        Object.keys(geometryGeoJson).forEach((key) => { index.push(key); });
        Object.keys(propertiesGeoJson).forEach((key) => { index.push(key); });
        return index;
    }
    // tslint:disable-next-line:no-any
    static GetDataWithIndexFromGeoJson(d: any): any {
        // let needIndex = true;
        // tslint:disable-next-line:no-any
        let dSet: any = [];
        Object.keys(d).forEach((key) => {
            if (key === 'features') {
                let theData = d[key];
                for (let data of theData) {
                    // tslint:disable-next-line:no-any
                    let theIndex: any = [];
                    // tslint:disable-next-line:no-any
                    let item: any = [];
                    let needIndex = true;
                    Object.keys(data).forEach((objKey) => {
                        Object.keys(data[objKey]).forEach((subKey) => {
                            let theItem = data[objKey][subKey];
                            if (typeof (theItem) === 'string' && theItem.length === 1) { return; }
                            if (needIndex) { theIndex.push(subKey); }
                            item.push(theItem);
                        });
                    });
                    if (needIndex) {
                        dSet.push(theIndex);
                        needIndex = false;
                    }
                    dSet.push(item);
                }
                return dSet;
            }
        });
        return dSet;
    }
    // tslint:disable-next-line:no-any
    constructor(d: any) {
        this.dJson = d;
        this.dMat = NGeoJSONFeatureService.GetDataWithIndexFromGeoJson(d);
        this.index = this.dMat[0];
        this.Init();
    }
    Init() {
        // this.dDescribe = this.GetDescribeByCol(this.index[0]).ToString();
        // this.dHist = this.GetHistByCol(this.index[0]);

        // https://earthquake.usgs.gov/earthquakes/feed/v1.0/geojson_detail.php
        for (let i of this.index) { this.array[i] = this.GetColbyIndex(i); }

    //     console.log(this.array);

        // tslint:disable-next-line:no-string-literal
        this.array['timeConverted'] = this.ConvertTime(this.array['Time_UT_']); // "Time_UT_ // time_
        this.ComputeTimelineIndex();

        // tslint:disable-next-line:no-string-literal
        let indexArray: number[] = []; for (let i = 0; i < this.array['timeConverted'].length; ++i) { indexArray.push(i); }

        this.ComputeArrayVis(indexArray);
    //     console.log(this);
    }
    ComputeArrayVis(indexArray: number[]) {
        this.arrayVis = {};
        for (let o of this.index) {
            this.arrayVis[o] = [];
            for (let i of indexArray) {
                this.arrayVis[o].push(this.array[o][i]);
            }
        }
    }
    GetHistByCol(colIndex: string, numBin: number = 20): number[][] {
        let data = this.GetDataByIndex(colIndex);
        return StatisticsUtility.GetDataForHistDataFromNumeric(data, numBin);
    }
    GetDataByIndex(colIndex: string) {
        let item = this.index.indexOf(colIndex);
        // tslint:disable-next-line:no-any
        let d: any = [];
        for (let i = 1; i < this.dMat.length; ++i) {
            d.push(this.dMat[i][item]);
        }
        return d;
    }
    GetColbyIndex(colIndex: string) {
        return this.GetDataByIndex(colIndex);
    }
    private ConvertTime(d: number[]) {
        let minYear = Number.MAX_SAFE_INTEGER;
        let maxYear = Number.MIN_SAFE_INTEGER;
        let dateArray: number[][] = [];
        for (let i = 0; i < d.length; ++i) {
            let date = new Date(+d[i]).toLocaleDateString().split('/');

            if (minYear > +date[2]) { minYear = +date[2]; }
            if (maxYear < +date[2]) { maxYear = +date[2]; }

            dateArray.push([+date[0], +date[1], +date[2]]);
        }
        // tslint:disable-next-line:no-string-literal
        this.array['yearDomain'] = [minYear, maxYear];
        return dateArray;
    }
    private ComputeTimelineIndex() {
        let timeLineIndex = {};
        // tslint:disable-next-line:no-string-literal
        this.array['timeLineIndexArryForKey'] = [];
        // tslint:disable-next-line:no-string-literal
        for (let j = this.array['yearDomain'][0]; j <= this.array['yearDomain'][1]; ++j) {
            for (let i = 1; i < 13; ++i) {
                let d: string = j + '_' + i;
                timeLineIndex[d] = [];
                // tslint:disable-next-line:no-string-literal
                this.array['timeLineIndexArryForKey'].push(d);
            }
        }
        // tslint:disable-next-line:no-string-literal
        this.array['timeLineIndex'] = timeLineIndex;

        // tslint:disable-next-line:no-string-literal
        for (let i = 0; i < this.array['timeConverted'].length; ++i) {
            // tslint:disable-next-line:no-string-literal
            let d: string = this.array['timeConverted'][i][2] + '_' + this.array['timeConverted'][i][0];
            // tslint:disable-next-line:no-string-literal
            this.array['timeLineIndex'][d].push(i);
        }
    //     console.log(this);
        // console.log
    }
    // GetDescribeByCol(colIndex: string): Describe {
    //     let data = this.GetDataByIndex(colIndex);
    //     return StatisticsUtility.DescribeNumeric(data, colIndex);
    // }

}