export const normalize = (v: NVector3): NVector3 => {
  const len: number = Math.sqrt(v.x * v.x + v.y * v.y + v.z * v.z);
  if (len < 0.0001) {
    return new NVector3(v.x, v.y, v.z);
  }
  return new NVector3(v.x / len, v.y / len, v.z / len);
}

export const distanceToXY = (x0: number, y0: number, x1: number, y1: number) => {
    return Math.sqrt((x0 - x1) * (x0 - x1) + (y0 - y1) * (y0 - y1));
}

export const distance = (v0: NVector3, v1: NVector3): number => {
  return Math.sqrt((v0.x - v1.x) * (v0.x - v1.x) + (v0.y - v1.y) * (v0.y - v1.y) + (v0.z - v1.z) * (v0.z - v1.z));
  // return NWebassemblyController.ctrller.Distance(v0.x, v0.y, v0.z, v1.x, v1.y, v1.z);
}

export const distanceByArray = (v0: number[], v1: number[]): number => {
  return Math.sqrt((v0[0] - v1[0]) * (v0[0] - v1[0]) + (v0[1] - v1[1]) * (v0[1] - v1[1]) + (v0[2] - v1[2]) * (v0[2] - v1[2]));
}

export const distance2 = (v1: NVector3, v2: NVector3): number => {
  return (v1.x - v2.x) * (v1.x - v2.x) + (v1.y - v2.y) * (v1.y - v2.y) + (v1.z - v2.z) * (v1.z - v2.z);
}

export const removeVecsWithinDis = (vs: NVector3[], tor: number = 10): NVector3[] => {
  let vecs: NVector3[] = [];
  for (let i = 0; i < vs.length - 1; ++i) {
    let v0 = vs[i];
    if (v0.d === false) { continue; }
    for (let j = i + 1; j < vs.length; ++j) {
      if (distance(v0, vs[j]) < tor) { vs[j].d = false; }
    }
  }
  for (let i = 0; i < vs.length; ++i) { if (vs[i].d === true) { vecs.push(vs[i]); } }
  // vecs.push(vs[vs.length - 1]);
  return vecs;
}

export const meterFromGeoGPSPos = (gV0: NVector3, gV1: NVector3): number => {
    let R = 6378.137; // Radius of earth in KM // a = Equatorial radius (6,378.1370 km) // b = Polar radius (6,356.7523 km)
    let dLat = gV1.lat * Math.PI / 180 - gV0.lat * Math.PI / 180;
    let dLon = gV1.long * Math.PI / 180 - gV0.long * Math.PI / 180;
    let a = Math.sin(dLat / 2) * Math.sin(dLat / 2) + Math.cos(gV0.lat * Math.PI / 180) * Math.cos(gV1.lat * Math.PI / 180) * Math.sin(dLon / 2) * Math.sin(dLon / 2);
    let c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    let d = R * c;
    return d * 1000; // meters
}
export const AABBintersect2D = (v0: NVector3[], v1: NVector3[]): boolean => {
  const v0minX = v0[0].x;
  const v0maxX = v0[1].x;
  const v1minX = v1[0].x;
  const v1maxX = v1[1].x;
  const v0minY = v0[0].y;
  const v0maxY = v0[1].y;
  const v1minY = v1[0].y;
  const v1maxY = v1[1].y;
  // const v0minZ = 0;
  // const v0maxZ = 0;
  // const v1minZ = 0;
  // const v1maxZ = 0;
  return (v0minX <= v1maxX && v0maxX >= v1minX) &&
  (v0minY <= v1maxY && v0maxY >= v1minY); // &&
  // (v0minZ <= v1maxZ && v0maxZ >= v1minZ);
};
export const dotProduct = (v1: NVector3, v2: NVector3): number => {
  return v1.x * v2.x + v1.y * v2.y + v1.z * v2.z;
}

export const crossProduct = (v1: NVector3, v2: NVector3): NVector3 => {
  return new NVector3(v1.y * v2.z - v1.z * v2.y, v1.z * v2.x - v1.x * v2.z, v1.x * v2.y - v1.y * v2.x);
}

export const getBoundingVecs = (v0: NVector3, v1: NVector3): NVector3[] => {
    let listOfVec: NVector3[] = [];
    let x: number = 0;
    let y: number = 0;
    let z: number = 0;
    if (v0.x < v1.x) { x = v0.x; } else { x = v1.x; }
    if (v0.y < v1.y) { y = v0.y; } else { y = v1.y; }
    if (v0.z < v1.z) { z = v0.z; } else { z = v1.z; }
    let nv0 = new NVector3(x, y, z);
    if (v0.x > v1.x) { x = v0.x; } else { x = v1.x; }
    if (v0.y > v1.y) { y = v0.y; } else { y = v1.y; }
    if (v0.z > v1.z) { z = v0.z; } else { z = v1.z; }
    let nv1 = new NVector3(x, y, z);
    listOfVec.push(new NVector3(nv0.x, nv0.y, nv0.z));
    listOfVec.push(new NVector3(nv1.x, nv0.y, nv0.z));
    listOfVec.push(new NVector3(nv1.x, nv1.y, nv0.z));
    listOfVec.push(new NVector3(nv0.x, nv1.y, nv0.z));
    return listOfVec;
}

export const isInsideOfVecs = (v: NVector3, vecs: NVector3[]): boolean => {
    // ray-casting algorithm based on https://github.com/substack/point-in-polygon
    // http://www.ecse.rpi.edu/Homepages/wrf/Research/Short_Notes/pnpoly.html
    let x = v.x, y = v.y;
    let inside = false;
    for (let i = 0, j = vecs.length - 1; i < vecs.length; j = i++) {
      let xi = vecs[i].x, yi = vecs[i].y;
      let xj = vecs[j].x, yj = vecs[j].y;
      let intersect = yi > y !== yj > y && x < (xj - xi) * (y - yi) / (yj - yi) + xi;
      if (intersect) {
        inside = !inside;
      }
    }
    return inside;
  }
  export const isInsideOfVecsByArray = (v: number[], vecs: number[][]): boolean => {
    // ray-casting algorithm based on https://github.com/substack/point-in-polygon
    // http://www.ecse.rpi.edu/Homepages/wrf/Research/Short_Notes/pnpoly.html
    let x = v[0], y = v[1];
    let inside = false;
    for (let i = 0, j = vecs.length - 1; i < vecs.length; j = i++) {
      let xi = vecs[i][0], yi = vecs[i][1];
      let xj = vecs[j][0], yj = vecs[j][1];
      let intersect = yi > y !== yj > y && x < (xj - xi) * (y - yi) / (yj - yi) + xi;
      if (intersect) {
        inside = !inside;
      }
    }
    return inside;
  }
  export const isInsideOfVecsByCrossing = (v: NVector3, w: number, h: number, vs: NVector3[]) => {
    if (isInsideOfVecs(new NVector3(v.x - w * 0.5, v.y - h * 0.5), vs)) { return true; }
    if (isInsideOfVecs(new NVector3(v.x + w * 0.5, v.y - h * 0.5), vs)) { return true; }
    if (isInsideOfVecs(new NVector3(v.x + w * 0.5, v.y + h * 0.5), vs)) { return true; }
    if (isInsideOfVecs(new NVector3(v.x - w * 0.5, v.y + h * 0.5), vs)) { return true; }
    return false;
  }
  export const isInsideOfVecsByConcaveHull = (v: NVector3, vs: NVector3[]) => {
    // let vsNum: number[][] = [];
    // for (let i = 0; i < vs.length; ++i) {
    //   vsNum.push([vs[i].x, vs[i].y]);
    // }
  
    // let result = ConcaveCore.hull(vsNum, 20)
    // console.log(vs.length);
    // console.log(result);
  
    // for (let i = 0; i < result.length; ++i) {
    //   new CPoint(result[i][0], result[i][1]);
    // }
  }
  // Convex hull
  // https://en.wikibooks.org/wiki/Algorithm_Implementation/Geometry/Convex_hull/Monotone_chain
  export const isInsideOfVecsByConvexHull = (v: NVector3, vecs: NVector3[]): boolean => {
    vecs = convexHull(vecs);
    if (isInsideOfVecs(v, vecs)) { return true; } else { return false; }
  }
  export const convexHull = (vs: NVector3[]): NVector3[] => {
    vs.sort(function (a: NVector3, b: NVector3) {
      return a.x === b.x ? a.y - b.y : a.x - b.x;
    });
    let lower: NVector3[] = [];
    for (let i = 0; i < vs.length; i++) {
      while (lower.length >= 2 && cross(lower[lower.length - 2], lower[lower.length - 1], vs[i]) <= 0) { lower.pop(); }
      lower.push(vs[i]);
    }
    let upper: NVector3[] = [];
    for (let i = vs.length - 1; i >= 0; i--) {
      while (upper.length >= 2 && cross(upper[upper.length - 2], upper[upper.length - 1], vs[i]) <= 0) { upper.pop(); }
      upper.push(vs[i]);
    }
    upper.pop(); lower.pop();
    for (let i = 0; i < upper.length; ++i) { lower.push(upper[i]); }
    return lower;
  }
  export const cross = (O: NVector3, A: NVector3, B: NVector3): number => {
    return (A.x - O.x) * (B.y - O.y) - (A.y - O.y) * (B.x - O.x);
  }
  export const cross_A_B_OUT = (a: NVector3, b: NVector3, out: NVector3) => {
    const ax = a.x, ay = a.y, az = a.z, bx = b.x, by = b.y, bz = b.z;
    out.x = ay * bz - az * by;
    out.y = az * bx - ax * bz;
    out.z = ax * by - ay * bx;
    return out;
  }
  export const CheckBoundIntersection = (vs: NVector3[], p0: NVector3, p1: NVector3) => {
    for (let i = 0; i < vs.length - 1; ++i) {
      if (checkLineIntersection(vs[i], vs[i + 1], p0, p1)) {
        return true;
      }
    }
    return false;
  }
  
// http://jsfiddle.net/justin_c_rounds/Gd2S2/light/
export const checkLineIntersection = (p0: NVector3, p1: NVector3, p2: NVector3, p3: NVector3) => {
  // if the lines intersect, the result contains the x and y of the intersection (treating the lines as infinite) and booleans for whether line segment 1 or line segment 2 contain the point
  let denominator: number = 0, a: number = 0, b: number = 0, numerator1: number = 0, numerator2: number = 0;
  let onLine0: boolean = false, onLine1: boolean = false;
  denominator = ((p3.y - p2.y) * (p1.x - p0.x)) - ((p3.x - p2.x) * (p1.y - p0.y));
  let result: NVector3 = new NVector3(0, 0, 0);
  if (denominator === 0) { return undefined; }
  a = p0.y - p2.y;
  b = p0.x - p2.x;
  numerator1 = ((p3.x - p2.x) * a) - ((p3.y - p2.y) * b);
  numerator2 = ((p1.x - p0.x) * a) - ((p1.y - p0.y) * b);
  a = numerator1 / denominator;
  b = numerator2 / denominator;

  // if we cast these lines infinitely in both directions, they intersect here:
  result.x = p0.x + (a * (p1.x - p0.x));
  result.y = p0.y + (a * (p1.y - p0.y));
  /*
          // it is worth noting that this should be the same as:
          x = p2.x + (b * (p3.x - p2.x));
          y = p2.x + (b * (p3.y - p2.y));
          */
  // if line1 is a segment and line2 is infinite, they intersect if:
  if (a > 0 && a < 1) {
    onLine0 = true;
  }
  // if line2 is a segment and line1 is infinite, they intersect if:
  if (b > 0 && b < 1) {
    onLine1 = true;
  }
  // if line1 and line2 are segments, they intersect if both of the above are true
  if (onLine0 && onLine1) {
    return result;
  } else { return undefined; }
}
// http://www.sunshine2k.de/coding/java/Polygon/Convex/polygon.htm
export const checkConvexity = (vs: NVector3[]) => {
  if (vs.length < 3) { return false; }

  let p: NVector3;
  let v: NVector3;
  let u: NVector3;
  let res = 0;

  for (let i = 0; i < vs.length; i++) {
    p = vs[i];
    let tmp = vs[(i + 1) % vs.length];
    v = new NVector3(tmp.x - p.x, tmp.y - p.y, 0);
    u = vs[(i + 2) % vs.length];

    if (i === 0) { // in first loop direction is unknown, so save it in res
      res = u.x * v.y - u.y * v.x + v.x * p.y - v.y * p.x;
    } else {
      let newres = u.x * v.y - u.y * v.x + v.x * p.y - v.y * p.x;
      if ((newres > 0 && res < 0) || (newres < 0 && res > 0)) { return false; }
    }
  }
  return true;
}

export const getBoundaryByVecWidthHeight = (v: NVector3, width: number, height: number): NVector3[] => {
  let boundarVecsSub = [];
  boundarVecsSub.push(new NVector3(v.x, v.y, v.z));
  boundarVecsSub.push(new NVector3(v.x + width, v.y, v.z));
  boundarVecsSub.push(new NVector3(v.x + width, v.y + height, v.z));
  boundarVecsSub.push(new NVector3(v.x, v.y + height, v.z));
  return boundarVecsSub;
}
export const getBoundingByVecs = (vs: NVector3[]): NVector3[] => {
  let listOfVec: NVector3[] = [];
  let xMin: number = Number.MAX_SAFE_INTEGER;
  let yMin: number = Number.MAX_SAFE_INTEGER;
  let zMin: number = Number.MAX_SAFE_INTEGER;

  let xMax: number = Number.MIN_SAFE_INTEGER;
  let yMax: number = Number.MIN_SAFE_INTEGER;
  let zMax: number = Number.MIN_SAFE_INTEGER;
  for (let i = 0; i < vs.length; ++i) {

    if (xMin > vs[i].x) { xMin = vs[i].x; }
    if (yMin > vs[i].y) { yMin = vs[i].y; }
    if (zMin > vs[i].z) { zMin = vs[i].z; }

    if (xMax < vs[i].x) { xMax = vs[i].x; }
    if (yMax < vs[i].y) { yMax = vs[i].y; }
    if (zMax < vs[i].z) { zMax = vs[i].z; }

  }
  listOfVec.push(new NVector3(xMin, yMin, zMin));
  listOfVec.push(new NVector3(xMax, yMax, zMax));
  return listOfVec;
}
export const getMinMaxVecFromVecs = (vec: NVector3[]): NVector3[] => {
  let minX = Number.MAX_VALUE, minY = Number.MAX_VALUE, minZ = Number.MAX_VALUE;
  let maxX = Number.MIN_VALUE, maxY = Number.MIN_VALUE, maxZ = Number.MIN_VALUE;
  for (let v of vec) {
    if (minX > v.x) { minX = v.x; } if (minY > v.y) { minY = v.y; } if (minZ > v.z) { minZ = v.z; }
    if (maxX < v.x) { maxX = v.x; } if (maxY < v.y) { maxY = v.y; } if (maxZ < v.z) { maxZ = v.z; }
  }
  return [new NVector3(minX, minY, minZ), new NVector3(maxX, maxY, maxZ)];
}

