export const isInsideOfVecsWtihVecs = (vs: NVector3[], vecs: NVector3[]): boolean => {
  let inside: boolean = true;
  for (let i = 0; i < vs.length; i++) {
    inside = isInsideOfVecs(vs[i], vecs);
    if (!inside) {
      return false;
    }
  }
  return true;
}
export const isInsideOfVecWithHeight = (mv: NVector3, tv: NVector3, width: number, height: number) => {
  if (tv.x < mv.x && mv.x < tv.x + width && tv.y < mv.y && mv.y < tv.y + height) { return true; }
  return false;
}

