
const themeRed = require('./theme/red.json');
const themePurple = require('./theme/purple.json');
const themeBlue = require('./theme/blue.json');
const themeGreen = require('./theme/green.json');
const themeOrange = require('./theme/orange.json');
const themeGray = require('./theme/gray.json');
const themeDarkgray = require('./theme/darkGray.json');
const themeBlack = require('./theme/black.json');

export class Asset {
    // tslint:disable-next-line:no-any
    themeRed: any; themePurple: any; themeBlue: any; themeGreen: any; themeOrange: any; themeGray: any; themeDarkgray: any; themeBlack: any;
    constructor() {
        this.ImportAssets();
    }
    async ImportAssets() {
        this.themeRed = themeRed;
        this.themePurple = themePurple;
        this.themeBlue = themeBlue;
        this.themeGreen = themeGreen;
        this.themeOrange = themeOrange;
        this.themeGray = themeGray;
        this.themeDarkgray = themeDarkgray;
        this.themeBlack = themeBlack;
    }
}