    public initLines(toScreen: (x: number, y: number) => number[] ) {
        this.linesVecs = [];
        this.cols = [];
        this.streetName = [];

        // let col = "#00ff00";
        let col = "rgba(0,255,0, 0.5)";

        let nameOfStreet = this.dataList[0][1];

        let lon = this.dataList[0][4];
        let lat = this.dataList[0][3];
        // let v = this.definition.app.projection.ToScreen(x, y);
        let sv = toScreen(lon, lat);

        let lineVecs: NVector3[] = [];
        let vec = new NVector3(sv[0], sv[1], 0);
        vec.long = lon;
        vec.lat = lat;
        lineVecs.push(vec);
        // this.cols.push(GetRandomRGB());
        this.cols.push(col);
        this.streetName.push(nameOfStreet);

        for(let i = 0 ; i < this.dataList.length ; i++) {

            lon = this.dataList[i][4];
            lat = this.dataList[i][3];
            sv = toScreen(lon, lat);

            if (nameOfStreet === this.dataList[i][1]) {
                // ctx.lineTo(v0[0], v0[1]);
                let vec = new NVector3(sv[0], sv[1], 0);
                vec.long = lon;
                vec.lat = lat;
                lineVecs.push(vec);

            } else {
                nameOfStreet = this.dataList[i][1];
                // ctx.moveTo(v0[0], v0[1]);
                this.linesVecs.push(lineVecs);
                // this.cols.push(GetRandomRGB());
                this.cols.push(col);
                this.streetName.push(nameOfStreet);

                lineVecs = [];
                let vec = new NVector3(sv[0], sv[1], 0);
                vec.long = lon;
                vec.lat = lat;
                lineVecs.push(vec);

            }
        }
        this.linesVecs.push(lineVecs); // for the lst line

        // console.log(this);

        // .. line

        const nLines = [];

        for (let j = 0 ; j < this.linesVecs.length; ++j) {

            for (let i = 1 ; i < this.linesVecs[j].length; ++i) {
                nLines.push(new NLine(this.linesVecs[j][i-1], this.linesVecs[j][i]));
            }

        }


        // console.log('nLines', nLines);
        // getLinesFromArray(this.lines);
        // this.lineTest = getLinesFromArray(this.lines);
        this.lineTest = nLines;

    }
