    public renderRaw(ctx: CanvasRenderingContext2D, toScreen: (x: number, y: number) => number[]) {
        if (!this.isRenderable) { return; }
        ctx.fillStyle = "#ff0000";
        
        ctx.strokeStyle = "#0ff000";
        ctx.lineWidth = 1;

        // console.log(this.dataList);
        // console.log(ctx);

        let nameOfStreet = this.dataList[0][1];

        let x = this.dataList[0][4];
        let y = this.dataList[0][3];
        // console.log(toScreen(100, 100));
        let v = toScreen(x, y);

        ctx.beginPath();
        ctx.moveTo(v[0], v[1]);

        for(let i = 0 ; i < this.dataList.length ; i++) {

            const x0 = this.dataList[i][4];
            const y0 = this.dataList[i][3];
            const v0 = toScreen(x0, y0);

            if (nameOfStreet === this.dataList[i][1]) {
                ctx.lineTo(v0[0], v0[1]);
            } else {
                nameOfStreet = this.dataList[i][1];
                ctx.stroke();
                ctx.beginPath();
                ctx.moveTo(v0[0], v0[1]);
            }
        }
        ctx.stroke();
    }
    public ComputeCSV(data: any){
        data =  this.processData(data);
        for(let i = 0 ; i < data.length; ++i){
            // console.log(data[i]);
        }
    }
    private processData(allText: any) {
        const allTextLines = allText.split(/\r\n|\n/);
        const headers = allTextLines[0].split(',');
        const lines = [];

        for (let i = 1; i < allTextLines.length; i++) {
            const data = allTextLines[i].split(',');
            if (data.length === headers.length) {

                const tarr = [];
                for (let j = 0; j < headers.length; j++) {
                    const dataTemp = +data[j]
                    if(isNaN(dataTemp)){
                        tarr.push(data[j]);
                    } else {
                        tarr.push(dataTemp);
                    }
                    
                }
                lines.push(tarr);
            }
        }
        this.dataList = lines;
        this.isRenderable = true;
        return lines;
    }
