export class NGeoJSONPolygon {
    static d: number[];
    static geoJsonGeoType = ['Point', 'MultiPoint', 'LineString', 'MultiLineString', 'Polygon', 'MultiPolygon', 'GeometryCollection'];
    // tslint:disable-next-line:no-any
    dJson: any;
    // tslint:disable-next-line:no-any
    dMat: any;
    // tslint:disable-next-line:no-any
    index: any;
    dHist: number[][];
    dDescribe: string[];
    dT: number[];
    dA: number[];
    dB: number[];
    dC: number[];
    dD: number[];
    dE: number[];
    dF: number[];
    posLn: number[];
    // tslint:disable-next-line:no-any
    static GetIndexFromGeoJson(d: any) {
        // tslint:disable-next-line:no-any
        let geometryGeoJson: any = [], propertiesGeoJson: any = [];
        Object.keys(d).forEach((key) => {
            if (key === 'features') {
                let theData = d[key];
                for (let data of theData) {
                    geometryGeoJson = data.geometry;
                    propertiesGeoJson = data.properties;
                    break;
                }
            }
        });
        let index: string[] = [];
        Object.keys(geometryGeoJson).forEach((key) => { index.push(key); });
        Object.keys(propertiesGeoJson).forEach((key) => { index.push(key); });
        return index;
    }
    // tslint:disable-next-line:no-any
    static GetDataWithIndexFromGeoJson(d: any): any {
        // let needIndex = true;
        // tslint:disable-next-line:no-any
        let dSet: any = [];
        Object.keys(d).forEach((key) => {
            if (key === 'features') {
                let theData = d[key];
                for (let data of theData) {
                    // tslint:disable-next-line:no-any
                    let theIndex: any = [];
                    // tslint:disable-next-line:no-any
                    let item: any = [];
                    let needIndex = true;
                    Object.keys(data).forEach((objKey) => {
                        Object.keys(data[objKey]).forEach((subKey) => {
                            let theItem = data[objKey][subKey];
                            if (typeof (theItem) === 'string' && theItem.length === 1) { return; }
                            if (needIndex) { theIndex.push(subKey); }
                            item.push(theItem);
                        });
                    });
                    if (needIndex) {
                        dSet.push(theIndex);
                        needIndex = false;
                    }
                    dSet.push(item);
                }
                return dSet;
            }
        });
        return dSet;
    }
    // tslint:disable-next-line:no-any
    constructor(d: any) {
        this.dJson = d;
        this.dMat = NGeoJSONPolygon.GetDataWithIndexFromGeoJson(d);
        this.index = this.dMat[0];
    //     console.log(this.index);
        this.posLn = this.GetPTFloat32Array();
        NGeoJSONPolygon.d = this.posLn;

        // console.log(this.dJson);
        // console.log(this.index);
        // console.log(this.dDescribe);
        // console.log(this.dMat);

        // https://earthquake.usgs.gov/earthquakes/feed/v1.0/geojson_detail.php

        // this.dT = this.GetColbyIndex('coordinates'); //  rms sig nst dmin tz mag time
        // this.dA = this.GetColbyIndex(this.index[0]); // rms sig nst dmin tz
        // this.dB = this.GetColbyIndex('rms'); // rms sig nst dmin tz
        // this.dC = this.GetColbyIndex('sig'); // rms sig nst dmin tz

        // this.dD = this.GetColbyIndex('nst'); // rms sig nst dmin tz mag time
        // this.dE = this.GetColbyIndex('mag'); // rms sig nst dmin tz mag time
        // this.dF = this.GetColbyIndex('dmin'); // rms sig nst dmin tz mag time

        // console.log(this.dD);
        // CScatter.dA = this.dA;
        // CScatter.dB = this.dB;
        // CScatter.dC = this.dC;

        // CLineChart.dA = this.dT;
        // CLineChart.dB = this.dD;

        // CAreaChart.dT = this.dT;
        // CAreaChart.dA = this.dD;
        // CAreaChart.dB = this.dE;
        // CAreaChart.dC = this.dF;
    }
    GetPTFloat32Array() {
        let n = [1];
        let d = this.GetDataByIndex(this.index[this.index.length - 1]);
        // console.log(d);
        let ddd = 0;
        for (let k = 0; k < d.length; ++k) {

            if (Array.isArray(d[k])) {
                //

                // if()
                for (let j = 0; j < d[k].length; ++j) {
                    // console.log(d[k][j]);
                    if (Array.isArray(d[k][j])) {
                        for (let i = 0; i < d[k][j].length; ++i) {

                            if (Array.isArray(d[k][j][i])) {
                                for (let e = 0; e < d[k][j][i].length; ++e) {
                                    n.push(d[k][j][i][e][0], d[k][j][i][e][1], 10.5);
                                }
                            }
                            // console.log(d[k][j][i][1], d[k][j][i][0], 0);
                        }

                        if (ddd++ > 100) {
                            // break;
                        }
                    } else {
                        //
                    }
                }
            }
            // break;
        }
        n.shift();
    //     console.log(n);
        return n;
    }
    GetDataByIndex(colIndex: string) {
        let item = this.index.indexOf(colIndex);
        let d = [];
        for (let i = 1; i < this.dMat.length; ++i) {
            d.push(this.dMat[i][item]);
        }
        return d;
    }
    GetColbyIndex(colIndex: string) {
        return this.GetDataByIndex(colIndex);
    }
    GetDescribeByCol(colIndex: string): Describe {
        let data = this.GetDataByIndex(colIndex);
        return StatisticsUtility.DescribeNumeric(data, colIndex);
    }
    GetHistByCol(colIndex: string, numBin: number = 20): number[][] {
        let data = this.GetDataByIndex(colIndex);
        return StatisticsUtility.GetDataForHistDataFromNumeric(data, numBin);
    }

}