import { NVector3 } from '.';


export const getLinesFromArray = (lines: number[][][]) =>{

    const lns: NLine[] = [];
    let ln: NLine;
    let v0: NVector3;
    let v1: NVector3;

    for (let j = 0; j < lines.length; ++j ) {

        for (let i = 1; i < lines[j].length; ++i ) {
            v0 = new NVector3(lines[j][i -1][0], lines[j][i -1][1], 0 );
            v1 = new NVector3(lines[j][i][0], lines[j][i][1], 0 );
            ln = new NLine(v0, v1);
            lns.push(ln);
        }
    }
//     console.log(lines);
//     console.log(lns);
    return lns;
}



export class NLine{
    public p0: NVector3;
    public p1: NVector3;
    constructor(p0: NVector3, p1: NVector3) {
        this.p0 = p0;
        this.p1 = p1;
    }
    
    
}